:root {
    font-family: sans-serif;
    font-size: 16px;
}
.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.btn {
    border-radius: 4px;
    font-size: 14px;
    border: none;
    padding: 1rem 1.5rem 1rem 1.5rem;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-style: normal;
    text-decoration: none;
    color: #000;
}
.btn-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.download {
    background: #64baff;
}
.star {
    background: #d4d4d4;
}
.download:hover {
    background: #3689e6;
}
.star:hover {
    background: #abacae;
}
.download-img {
    height: 24px;
}
.star-img {
    height: 24px;
}
.desc {
    display: none;
    text-align: center;
}
.thanks{
    text-align: center;
}
@media screen and (min-width: 700px) {
    .btn-box {
        flex-direction: row;
    }
    .btn {
        margin-left: 1rem;
    }
    .desc {
        display: block;
    }
}